import React, {ComponentElement, ComponentType, ReactNode} from "react";
import styles from "./FormControls.module.sass";
import {BaseFieldProps, Field, WrappedFieldProps} from "redux-form";
import { FieldValidatorType } from "../../Controls/Validators/Validators";
import {CommonFieldProps} from "redux-form/lib/Field";

const Element =
    (Element: string) =>
            ({input,
                 meta: {touched, error},
                 ...props}: WrappedFieldProps) => {
    const hasError = touched && error;
    return (
        <div className={styles.formControl + " " + (hasError ? styles.error : "")}>
            <Element {...error} {...props} />
            {hasError && <span> {error} </span>}
        </div>
    );
};

export const Textarea: React.FC<WrappedFieldProps> = Element("textarea");
export const Input: React.FC<WrappedFieldProps> = Element("input");


export function createField<FormKeyType extends string> (placeholder: string | undefined,
                            type: string,
                            name: FormKeyType,
                            component: React.FC<WrappedFieldProps>,
                            validator: Array<FieldValidatorType>,
                            props = {},
                            text = '') {
   return <div>
        <label>
            <Field placeholder={placeholder} type={type} name={name} component={component}
                   validate={validator}
                {...props}/>
        </label>{text}
    </div>
};

