import React from 'react';
import s from './MyPosts.module.sass';
import Post from "./Post/Post";
import {AddNewPostForm} from "./AddNewPostForm";
import {PostType} from "../../../types/types";

type PropsType = {
    posts: PostType
    addPost: (values: any) => void
}

const MyPosts: React.FC<PropsType> = React.memo(props => {

    let postMap = new Map;

    // @ts-ignore
    let postElements = props.posts.map((p) => <Post message={p.message} likeCount={p.likeCount} key={p.id}
                                                        id={p.id}/>);

    let addNewPost = (values: { newPost: any; }) => {
        props.addPost(values.newPost);
    };

    return (
        <div className={s.posts}>
            <h3 className={s.posts_h3}>My posts</h3>
            <div className={s.posts_form}>
                <div className={s.form}>
                    // @ts-ignore
                    <AddNewPostForm onSubmit={addNewPost}/>
                </div>
            </div>
            <div className={s.post}>
                {postElements}
            </div>
        </div>
    );
});

export default MyPosts;